'use strict';

/*
* When adding modules to the namespace, ensure that they are added in alphabetical order according to module name.
*/

// MODULES //

import setReadOnly from '@stdlib/utils/define-read-only-property';


// MAIN //

/**
* Top-level namespace.
*
* @namespace fs
*/
var fs = {};

/**
* @name appendFile
* @memberof fs
* @readonly
* @type {Function}
* @see {@link module:@stdlib/fs/append-file}
*/
import appendFile from './../append-file';
setReadOnly( fs, 'appendFile', appendFile );

/**
* @name close
* @memberof fs
* @readonly
* @type {Function}
* @see {@link module:@stdlib/fs/close}
*/
import close from './../close';
setReadOnly( fs, 'close', close );

/**
* @name exists
* @memberof fs
* @readonly
* @type {Function}
* @see {@link module:@stdlib/fs/exists}
*/
import exists from './../exists';
setReadOnly( fs, 'exists', exists );

/**
* @name open
* @memberof fs
* @readonly
* @type {Function}
* @see {@link module:@stdlib/fs/open}
*/
import open from './../open';
setReadOnly( fs, 'open', open );

/**
* @name readDir
* @memberof fs
* @readonly
* @type {Function}
* @see {@link module:@stdlib/fs/read-dir}
*/
import readDir from './../read-dir';
setReadOnly( fs, 'readDir', readDir );

/**
* @name readFile
* @memberof fs
* @readonly
* @type {Function}
* @see {@link module:@stdlib/fs/read-file}
*/
import readFile from './../read-file';
setReadOnly( fs, 'readFile', readFile );

/**
* @name readFileList
* @memberof fs
* @readonly
* @type {Function}
* @see {@link module:@stdlib/fs/read-file-list}
*/
import readFileList from './../read-file-list';
setReadOnly( fs, 'readFileList', readFileList );

/**
* @name readJSON
* @memberof fs
* @readonly
* @type {Function}
* @see {@link module:@stdlib/fs/read-json}
*/
import readJSON from './../read-json';
setReadOnly( fs, 'readJSON', readJSON );

/**
* @name readNDJSON
* @memberof fs
* @readonly
* @type {Function}
* @see {@link module:@stdlib/fs/read-ndjson}
*/
import readNDJSON from './../read-ndjson';
setReadOnly( fs, 'readNDJSON', readNDJSON );

/**
* @name readWASM
* @memberof fs
* @readonly
* @type {Function}
* @see {@link module:@stdlib/fs/read-wasm}
*/
import readWASM from './../read-wasm';
setReadOnly( fs, 'readWASM', readWASM );

/**
* @name rename
* @memberof fs
* @readonly
* @type {Function}
* @see {@link module:@stdlib/fs/rename}
*/
import rename from './../rename';
setReadOnly( fs, 'rename', rename );

/**
* @name resolveParentPath
* @memberof fs
* @readonly
* @type {Function}
* @see {@link module:@stdlib/fs/resolve-parent-path}
*/
import resolveParentPath from './../resolve-parent-path';
setReadOnly( fs, 'resolveParentPath', resolveParentPath );

/**
* @name resolveParentPathBy
* @memberof fs
* @readonly
* @type {Function}
* @see {@link module:@stdlib/fs/resolve-parent-path-by}
*/
import resolveParentPathBy from './../resolve-parent-path-by';
setReadOnly( fs, 'resolveParentPathBy', resolveParentPathBy );

/**
* @name resolveParentPaths
* @memberof fs
* @readonly
* @type {Function}
* @see {@link module:@stdlib/fs/resolve-parent-paths}
*/
import resolveParentPaths from './../resolve-parent-paths';
setReadOnly( fs, 'resolveParentPaths', resolveParentPaths );

/**
* @name unlink
* @memberof fs
* @readonly
* @type {Function}
* @see {@link module:@stdlib/fs/unlink}
*/
import unlink from './../unlink';
setReadOnly( fs, 'unlink', unlink );

/**
* @name writeFile
* @memberof fs
* @readonly
* @type {Function}
* @see {@link module:@stdlib/fs/write-file}
*/
import writeFile from './../write-file';
setReadOnly( fs, 'writeFile', writeFile );


// EXPORTS //

export default fs;
