'use strict';

/**
* Read the contents of a directory.
*
* @module @stdlib/fs/read-dir
*
* @example
* import readDir from '@stdlib/fs/read-dir';
*
* function onRead( error, data ) {
*     if ( error ) {
*         console.error( error );
*     } else {
*         console.log( data );
*     }
* }
* readDir( __dirname, onRead );
*
* @example
* import { sync as readDirSync } from '@stdlib/fs/read-dir';
*
* var out = readDirSync( __dirname );
* if ( out instanceof Error ) {
*     throw out;
* }
* console.log( out );
*/

// MODULES //

import setReadOnly from '@stdlib/utils/define-nonenumerable-read-only-property';
import readDir from './async.js';
import sync from './sync.js';


// MAIN //

setReadOnly( readDir, 'sync', sync );


// EXPORTS //

export default readDir;
