'use strict';

/**
* Open a file.
*
* @module @stdlib/fs/open
*
* @example
* import { sync as closeSync } from '@stdlib/fs/close';
* import open from '@stdlib/fs/open';
*
* function onOpen( error, fd ) {
*     if ( error ) {
*         throw error;
*     }
*     closeSync( fd );
* }
* open( __filename, onOpen );
*
* @example
* import { sync as closeSync } from '@stdlib/fs/close';
* import { sync as openSync } from '@stdlib/fs/open';
*
* var fd = openSync( __filename );
* if ( fd instanceof Error ) {
*     throw fd;
* }
* closeSync( fd );
*/

// MODULES //

import setReadOnly from '@stdlib/utils/define-nonenumerable-read-only-property';
import main from './main.js';
import sync from './sync.js';


// MAIN //

setReadOnly( main, 'sync', sync );


// EXPORTS //

export default main;
